import React, {Component} from 'react'
import {connect} from 'react-redux'
import './banner.scss'
import Button from "../../form/button";
import Rectangle from '../../form/rectangle';
import Countdown from 'react-countdown-now'
import {stepLoaded} from '../../../actions'

class Banner extends Component{
  nextStep = () =>{
    const {stepLoaded} = this.props
    // const a = document.querySelector('body')
    // a.scrollTop = 1000;
    window.scrollTo(0, 800);
    if(localStorage.getItem('token')){
      stepLoaded(2)
    } else {
      stepLoaded(1)
    }
    
  }
  render () {
    return (
      <div className='banner'>
        <div className="banner__counter">
          <p className="banner__counter-text">  </p>
          <Countdown
      date={Date.now() + 1000000000}
      renderer={Rectangle}
    />
        </div>
        <div className="banner__text">
          <h1 className="banner__text-title">   
             </h1>
          <p className="banner__text-subtitle">     150,000   </p>
          <div className="banner__buttons">
            <Button title="" type="banner2" image="one" action={this.nextStep}/>
            <Button title="  " type="banner" image="two"/>
          </div>
        </div>
      </div>
    )
  }

}

export default connect(null, {stepLoaded})(Banner);