import React, {Component} from 'react'
import {connect} from 'react-redux'

import InputApi from "../../form/input-api";
import Button from "../../form/button";
import Switch from "../../form/switch";
import './how-to-get.scss'

import {transport, stepLoaded} from '../../../actions'

class HowToGet extends Component{
  state = {
    autoValue: '',
    autoSearch: '',
    trainValue:'',
    trainSearch: '',
    plainValue: '',
    plainSearch: '',
    auto_details: {
      typeId: 32,
      t_lat: Number,
      p_lng: Number,
      city: '',
      transfer: false
    },
    train_details: {
      typeId: 33,
      t_lat: Number,
      t_lng: Number,
      city: '',
      transfer: false,
      // skillOptions: ''
    },
    plain_details: {
      typeId: 34,
      t_lat: Number,
      t_lng: Number,
      city: '',
      transfer: false
    },
    error: false
  }
  componentDidMount () {
    const  {    auto_details,
      train_details,
      plain_details} = this.props

    if(Object.keys(auto_details).length !== 0) {
      this.setState({
        
        autoValue: auto_details.city,
        auto_details: auto_details
      })
    }  
    if(Object.keys(train_details).length !== 0) {
      this.setState({
        trainValue: train_details.city,
        train_details
      })
    } 
    if(Object.keys(plain_details).length !== 0) {
      this.setState({
        plainValue: plain_details.city,
        plain_details
      })
    } 
  }
  getAddressAuto = (body,state_name) => {
    console.log(body)
    this.setState(prevState => ({
      [state_name]: {
        ...prevState[state_name],
        city: body.formatted_address,
        t_lat: body.geometry.location.lat(),
        t_lng: body.geometry.location.lng()
      }
    }));
  };
  handleInputAuto = (e, value,search) =>{
    console.log(value)
    this.setState({[value]: e.target.value, [search]: e.target.value})
  }

  handleSelectAuto = (suggest,value,search,state_name) => {
    console.log(suggest)
    this.getAddressAuto(suggest,state_name)
    this.setState({[search]: "", [value]: suggest.formatted_address})
  }
  handleCheckBox = (value) => {
    this.setState(prevState => ({
      [value]: {
        ...prevState[value],
        transfer: !this.state[value].transfer
      }
    }));
  }
  nextStep = () => {
    const {transport, stepLoaded} = this.props
    const {auto_details, train_details, plain_details} = this.state

    if (auto_details.city.length !== 0 || train_details.city.length !== 0 || plain_details.city.length !== 0) {
      this.setState({
        error: false
      },transport([auto_details, train_details, plain_details]),
      stepLoaded(4))
    } else {
      this.setState({
        error: true
      })

      console.log('qwe')
      // transport([auto_details, train_details, plain_details])
      // stepLoaded(4)
    }
    
  }
  prevStep = () => {
    const {transport, stepLoaded} = this.props
    const {auto_details, train_details, plain_details} = this.state
    transport([auto_details, train_details, plain_details])
    stepLoaded(2)
  }
  render () {
    let errorText;
    const {    autoValue,
    autoSearch,
    trainValue,
    trainSearch,
    plainValue,
    plainSearch,
    auto_details,
  train_details,
  plain_details,
error} = this.state
    if ( error) {
      errorText = <span className="field-error">       </span>
    }
    return (
      <div className='how-to-get'>
        <h1 className="how-to-get-title"> </h1>
        <InputApi placeholder=" " title="" type="car" handleChange={(e)=>this.handleInputAuto(e,'autoValue','autoSearch')} handleSuggest={(suggest) =>this.handleSelectAuto(suggest,'autoValue','autoSearch','auto_details')}  search={autoSearch} value={autoValue}/>
        <InputApi placeholder=" " title="" type="train" handleChange={(e)=>this.handleInputAuto(e,'trainValue','trainSearch')} handleSuggest={(suggest) =>this.handleSelectAuto(suggest,'trainValue','trainSearch','train_details')}  search={trainSearch} value={trainValue}/>
        <div className="how-to-get__switch">
          <p>     </p>
          <Switch           
          name={''}
          // option={train_details.skillOptions}
          selectedOption={train_details.transfer}
          handleChange={() => this.handleCheckBox('train_details')}/>
        </div>
        <InputApi placeholder=" " title="" type="plain" handleChange={(e)=>this.handleInputAuto(e,'plainValue','plainSearch')} handleSuggest={(suggest) =>this.handleSelectAuto(suggest,'plainValue','plainSearch','plain_details')}  search={plainSearch} value={plainValue}/>
        <div className="how-to-get__switch">
          <p>     </p>
          <Switch  name={'2'}
          // option={train_details.skillOptions}
          selectedOption={plain_details.transfer}
          handleChange={()=>this.handleCheckBox('plain_details')}/>
        </div>
        {errorText}
        <div className="how-to-get-btn">
          <Button title="" action={this.prevStep}/>
          <Button title="" type="primary" action={this.nextStep}/>
        </div>
      </div>
    )
  }
}

const mapStateToProps = ({  auto_details,
  train_details,
  plain_details,}) => {
  return {
    auto_details,
    train_details,
    plain_details
  }
}

export default connect(mapStateToProps,{transport, stepLoaded})(HowToGet) 