import React, {Component} from 'react'
import './dropdown.scss'

class Dropdown extends Component {
  state = {
    subMenu: false,
  }
  componentDidMount() {
    document.addEventListener('mousedown', this.handleClickOutside);
    const {list} = this.props
  }

  componentWillUnmount() {
    document.removeEventListener('mousedown', this.handleClickOutside);
  }


  setWrapperRef =(node) => {
    this.wrapperRef = node;
  }
  handleClickOutside = (event) => {
    if (this.wrapperRef && !this.wrapperRef.contains(event.target)) {
      this.setState({
        subMenu: false
      })
    }
  }
  showSubmenu = () => {
    const {updateFilter} = this.props
    this.setState((state) => {
      return {
        subMenu: !state.subMenu
      }
    },updateFilter)
  }
  chooseName = (id, name) => {
    const {giveName} = this.props
    giveName(id,name)
    this.setState({
      firstTitle: name
    },
    this.showSubmenu )
  }

  render () {
    let menu,errorText;
    let dropdownStyle = 'dropdown'
    let iconStyleFocus;
    let iconStyle = 'dropdown-icon'
    let wrapperStyle = 'dropdown-wrapper'
    const {subMenu} = this.state
    const {title, list, handleSearch,firstTitle, error} = this.props
    const arrayList = list.map(item=>{
      return <li className="dropdown-submenu__item" key={item.id} onClick={()=>this.chooseName(item)}>{item.name}</li>
    })
    if (error) {
      errorText = <span className="field-error">{error}</span>
    }
    if (subMenu){
      menu = <div className="dropdown-submenu">
              <div className="dropdown-search">
                <input type="text" className="dropdown-search__input"  placeholder="" onChange={handleSearch} />
                <span className="dropdown-search__icon">
                <svg xmlns="http://www.w3.org/2000/svg" width="18" height="18" viewBox="0 0 18 18"><g><g><path fill="#a1a1a1" d="M11.73 11.73a5.762 5.762 0 1 1-8.148-8.148 5.762 5.762 0 0 1 8.149 8.149zm4.063 4.138l.075-.075.278.278v.075h-.075zM12.35 13.69l3.517 3.517 1.34-1.34-3.518-3.516A7.653 7.653 0 0 0 2.24 2.24 7.651 7.651 0 0 0 12.35 13.69z"/></g></g></svg>
                </span>
              </div>
              <div className="dropdown-submenu__list-wrapper">
                <ul className="dropdown-submenu__list">
                  {arrayList}
                </ul>
              </div>
            </div>
      
      iconStyle += ' dropdown-icon--rotate'
      dropdownStyle += ' dropdown--focus'
      iconStyleFocus += ' dropdown-icon__path'
      wrapperStyle += ' dropdown-wrapper--shadow'
    }
    return (
      <div className={wrapperStyle} tabindex="0" ref={this.setWrapperRef}>
        <div className="dropdown-title">{title}</div>
        <div className={dropdownStyle}  onClick={this.showSubmenu}>
          <span className="dropdown-name">{firstTitle}</span>
          <span className={iconStyle}>
            <svg xmlns="http://www.w3.org/2000/svg" width="12" height="8" viewBox="0 0 12 8"><g><g><path
            className={iconStyleFocus} fill="#a1a1a1" d="M1.41 0L6 4.58 10.59 0 12 1.41l-6 6-6-6z"/></g></g></svg>
          </span>
        </div>
        {menu}
        {errorText}
      </div>
    )
  }
}


export default Dropdown