import React, {Component} from 'react'
import {connect} from 'react-redux'
import SimpleReactValidator from 'simple-react-validator';
import axios from 'axios'
import AuthServices from '../../../services/AuthServices'
import Dropdown from "../../form/dropdown";
import Count from "../../form/count";
import Input from "../../form/input";
import CheckboxBlack from "../../form/checkbox-black";
import ImageLoaderList from "../../form/image-loader-list";
import Switch from "../../form/switch";
import Textarea from "../../form/textarea";
import Button from '../../form/button'
import './accommodation.scss'

import  {accomadationStep,stepLoaded,  filterAcomodation,
  updateFilterAcc} from '../../../actions'


class Accommodation extends Component  {
  state = {
    disabledAll:false,
    sleepBed: 1,
    error: false,
    arrayAccommodations: [
      {
        id: 0,
        typeId: '',
        firstTitle: '  ',
        cnt_bed: 1,
        price: '',
        options: [],
        drinks: false,
        has_animal: false,
        animal_description: '',
        attachments_id: [],
        error: null,
        attachments: [
          {
            id: 0,
            name: 'image',
            privew: null,
            active: true
          },
          {
            id: 1,
            name: 'image1',
            privew: null,
            active: false
          },
          {
            id: 2,
            name: 'image2',
            privew: null,
            active: false
          },
          {
            id: 3,
            name: 'image3',
            privew: null,
            active: false
          },
          {
            id: 4,
            name: 'image4',
            privew: null,
            active: false
          },
          {
            id: 5,
            name: 'image5',
            privew: null,
            active: false
          },
          {
            id: 6,
            name: 'image6',
            privew: null,
            active: false
          },
          {
            id: 7,
            name: 'image7',
            privew: null,
            active: false
          },
        ],
      }
    ],
  }

  componentWillMount() {
    this.validator = new SimpleReactValidator({  messages: {
      required: '    ',
      min: '    '
    }})
  }

  handleInput = (e, cost, i) => {
    let value = e.target.value;
    let stateCopy = Object.assign({}, this.state);
    stateCopy.arrayAccommodations = stateCopy.arrayAccommodations.slice();
    stateCopy.arrayAccommodations[i]= Object.assign({}, stateCopy.arrayAccommodations[i]);
    stateCopy.arrayAccommodations[i].price = value;
    this.setState(stateCopy)
  };
  addAccommodation = () => {
    if(!this.state.disabledAll) {
      this.setState(state => {
        let accommodation = {
          id: state.arrayAccommodations.length + 1,
          typeId: Number,
          firstTitle: '  ',
          cnt_bed: 1,
          price: '',
          options: [],
          drinks: false,
          has_animal: false,
          animal_description: '',
          attachments_id: [],
          error: null,
          attachments: [
            {
              id: 0,
              name: 'image',
              privew: null,
              active: true
            },
            {
              id: 1,
              name: 'image1',
              privew: null,
              active: false
            },
            {
              id: 2,
              name: 'image2',
              privew: null,
              active: false
            },
            {
              id: 3,
              name: 'image3',
              privew: null,
              active: false
            },
            {
              id: 4,
              name: 'image4',
              privew: null,
              active: false
            },
            {
              id: 5,
              name: 'image5',
              privew: null,
              active: false
            },
            {
              id: 6,
              name: 'image6',
              privew: null,
              active: false
            },
            {
              id: 7,
              name: 'image7',
              privew: null,
              active: false
            },
          ],
        }
        const list = state.arrayAccommodations.concat(accommodation);
  
        return {
          arrayAccommodations:list
        };
      });
    }
  };
  deleteAccommodation = (i) => {
    const {arrayAccommodations} = this.state
    if (arrayAccommodations.length > 1) {
      this.setState((state) => {
        const list = state.arrayAccommodations.filter((item, j) => i !== j);
        return {
          arrayAccommodations:list
        }
      })
    }

  }
  imageCheck = ()=>{
    const {arrayAccommodations} = this.state
    let stateCopy = Object.assign({}, this.state);
    stateCopy.arrayAccommodations = stateCopy.arrayAccommodations.slice();
    const res = arrayAccommodations.map((item,index)=>{
      if(item.attachments[2].privew == null){
        stateCopy.arrayAccommodations[index]= Object.assign({}, stateCopy.arrayAccommodations[index]);
        stateCopy.arrayAccommodations[index].error = true;
        this.setState(stateCopy)
        return stateCopy.arrayAccommodations[index]
      } else {
        stateCopy.arrayAccommodations[index]= Object.assign({}, stateCopy.arrayAccommodations[index]);
        stateCopy.arrayAccommodations[index].error = null;
        this.setState(stateCopy)
        return stateCopy.arrayAccommodations[index]
      }
      
    })
    const result = res.filter((item)=>{
      return item.error == true
    })
    console.log(result)
    return result
    
  }
  nextStep = () => {
    const {stepLoaded,accomadationStep} = this.props
    const {arrayAccommodations, disabledAll} = this.state
    if (disabledAll) {
      accomadationStep(arrayAccommodations)
      stepLoaded(7)
    }
    if (this.validator.allValid() && this.imageCheck().length == 0) {
      accomadationStep(arrayAccommodations)
      stepLoaded(7)
    } else {
      this.imageCheck()
      this.validator.showMessages();
      this.forceUpdate();
    }
  }
  prevStep = () => {
    const {stepLoaded,accomadationStep} = this.props
    const {arrayAccommodations} = this.state
    stepLoaded(5)
    accomadationStep(arrayAccommodations)
  }
  removeBeds = (index) => {
    console.log(index)
    let stateCopy = Object.assign({}, this.state);

    stateCopy.arrayAccommodations = stateCopy.arrayAccommodations.slice();
    let count = stateCopy.arrayAccommodations[index].cnt_bed - 1
    console.log(count)
    stateCopy.arrayAccommodations[index]= Object.assign({}, stateCopy.arrayAccommodations[index]);
    stateCopy.arrayAccommodations[index].cnt_bed =count;
    this.setState(stateCopy)
  }
  addBeds = (index) => {
    console.log(index)
    let stateCopy = Object.assign({}, this.state);
    
    stateCopy.arrayAccommodations = stateCopy.arrayAccommodations.slice();
    let count = stateCopy.arrayAccommodations[index].cnt_bed + 1
    console.log(count)
    stateCopy.arrayAccommodations[index]= Object.assign({}, stateCopy.arrayAccommodations[index]);
    console.log(stateCopy)
    stateCopy.arrayAccommodations[index].cnt_bed = count;
    console.log(stateCopy)
    this.setState(stateCopy)
  }
  showPhoto = (e, i,index) => {
    console.log(e.target.files)
    
    let file = e.target.files
    this.uploadPhotos('accommodationPhoto', file[0],index)
    if( file.length !== 0 && (file[0].type == 'image/png' || file[0].type == 'image/jpeg')) {
      console.log(file[0])
      let reader = new FileReader()
      reader.readAsDataURL(file[0])
      setTimeout(() => {
        let stateCopy = Object.assign({}, this.state);
        stateCopy.arrayAccommodations = stateCopy.arrayAccommodations.slice();
        stateCopy.arrayAccommodations[index]= Object.assign({}, stateCopy.arrayAccommodations[index]);
        stateCopy.arrayAccommodations[index].attachments[i].privew = reader.result;
         i == 7 ? stateCopy.arrayAccommodations[index].attachments[i].active = true:stateCopy.arrayAccommodations[index].attachments[i+1].active = true
        this.setState(stateCopy)
      }, 500)
    }
  }
  uploadPhotos = async(name,photo,index) => {
    axios.defaults.headers.common['x-api-token'] = 'Bearer ' + localStorage.token 
    const res = await AuthServices.uploadPhoto({fileType: name, file: photo})
    console.log(res)
    let stateCopy = Object.assign({}, this.state);
    stateCopy.arrayAccommodations = stateCopy.arrayAccommodations.slice();
    stateCopy.arrayAccommodations[index]= Object.assign({}, stateCopy.arrayAccommodations[index]);
    stateCopy.arrayAccommodations[index].attachments_id = stateCopy.arrayAccommodations[index].attachments_id.concat(res.data);
    this.setState(stateCopy)

  }
  closeImage = (e, i, index) =>{
    let stateCopy = Object.assign({}, this.state);
    console.log(this.state.arrayAccommodations[index].attachments)
    console.log(i)
    let list = this.state.arrayAccommodations[index].attachments.filter((item, j) => i !== j);
    console.log(list)
    const image = {
      id: i,
      name: `image${i}`,
      privew: null,
      active: this.state.arrayAccommodations[index].attachments[7].privew !== null ? true : false
    }
    let newList = list.concat(image)
    console.log(newList)
    stateCopy.arrayAccommodations = stateCopy.arrayAccommodations.slice();
    stateCopy.arrayAccommodations[index]= Object.assign({}, stateCopy.arrayAccommodations[index]);
    stateCopy.arrayAccommodations[index].attachments = newList;
    this.setState(stateCopy)
  }
  chooseName =({id,name},i) => {
    let stateCopy = Object.assign({}, this.state);
    stateCopy.arrayAccommodations = stateCopy.arrayAccommodations.slice();
    stateCopy.arrayAccommodations[i]= Object.assign({}, stateCopy.arrayAccommodations[i]);
    stateCopy.arrayAccommodations[i].typeId = id;
    stateCopy.arrayAccommodations[i].firstTitle = name;
    this.setState(stateCopy)
    if(name === ' ') {
      this.setState({
        disabledAll: true
      })
    } else {
      this.setState({
        disabledAll: false
      })
    }
  }
  handleChangeHuntList = (e,i) => {
    const newSelection = e.target.value
    console.log(i)
    let newSelectionArray;
    console.log(newSelection )
    console.log(this.state.arrayAccommodations[i].options)
    if (this.state.arrayAccommodations[i].options.indexOf(newSelection) > -1) {
      newSelectionArray = this.state.arrayAccommodations[i].options.filter(
        
        s => s !== newSelection 
        
      );
    } else {
      console.log('qwe')
      newSelectionArray = [...this.state.arrayAccommodations[i].options, newSelection];
    }
    console.log(newSelectionArray)
    let stateCopy = Object.assign({}, this.state);
    stateCopy.arrayAccommodations = stateCopy.arrayAccommodations.slice();
    stateCopy.arrayAccommodations[i]= Object.assign({}, stateCopy.arrayAccommodations[i]);
    stateCopy.arrayAccommodations[i].options = newSelectionArray;
    this.setState(stateCopy)
  }
  handleCheckBox = (i,value) => {
    let stateCopy = Object.assign({}, this.state);
    stateCopy.arrayAccommodations = stateCopy.arrayAccommodations.slice();
    stateCopy.arrayAccommodations[i]= Object.assign({}, stateCopy.arrayAccommodations[i]);
    console.log(value)
    console.log(stateCopy.arrayAccommodations[i][value])
    stateCopy.arrayAccommodations[i][value] = !stateCopy.arrayAccommodations[i][value];
    console.log(stateCopy.arrayAccommodations[i][value])
    this.setState(stateCopy)
  }
  handleTextarea = (e,i) => {
    let value = e.target.value
    let stateCopy = Object.assign({}, this.state);
    stateCopy.arrayAccommodations = stateCopy.arrayAccommodations.slice();
    stateCopy.arrayAccommodations[i]= Object.assign({}, stateCopy.arrayAccommodations[i]);
    stateCopy.arrayAccommodations[i].animal_description = value;
    this.setState(stateCopy)
  }
  componentDidMount () {
    const {accomodation_step} = this.props
    if (accomodation_step.length >0) {
      this.setState({
        arrayAccommodations: accomodation_step
      })
    }
  }
  clickHandle = (e) =>{
    e.target.value = null
  }
  search = (e)=>{
    const {filterAcomodation
     } = this.props
    const value = e.target.value
    filterAcomodation(value)
  }
  updateFilter = () => {
    const { updateFilterAcc} =this.props
    updateFilterAcc()
  }
  render() {
    const {sleepBed, features, arrayAccommodations, disabledAll} = this.state
    const {typeAmenities, step, typeAccommodation} = this.props
    let classDisabled = '';
    if (disabledAll) {
      classDisabled = 'disabled-block'
    }
    const accommodations = arrayAccommodations.map((accommodation, index) => {
      return <div className="accommodation" key={index}>
        <div className="accommodation__block">
          <div className="accommodation__block__row">
            <div className="accommodation__block__row-type">
              <Dropdown title=" " list={typeAccommodation} firstTitle={accommodation.firstTitle}  handleSearch={this.search} updateFilter={this.updateFilter} giveName={(item)=>this.chooseName(item,index)} error={this.validator.message('1', accommodation.typeId, 'required')}/>
              {/* <Dropdown title=" " list={typeAccommodation}/> */}
            </div>
            <div className={"accommodation__block__row-item " + classDisabled}>
              <Count  counterMax={99} title=" " name="search" counter={accommodation.cnt_bed} remove={()=>this.removeBeds(index)} add={()=>this.addBeds(index)} />
            </div>
            <div className={"accommodation__block__row-item " + classDisabled}>
              <Input
                    title=""
                    inputType="text"
                    name=""
                    value={accommodation.price}
                    mask={[ '', ' ',/[1-9]/, /\d/,   /\d/, /\d/,  /\d/, /\d/, /\d/, /\d/]}
                    handleChange={(e) =>this.handleInput(e, accommodation.cost, index)}
                    placeholder={""}
                    error={this.validator.message('2', accommodation.price, 'required|min:3')}
                  /> 
            </div>
          </div>
          <div className={classDisabled}>
            <ImageLoaderList id={`accomodation${index}`} images={accommodation.attachments} handle={(e,i)=>this.showPhoto(e,i,index)} close={(e,i)=>this.closeImage(e,i,index)} text={true} clickHandle={this.clickHandle} />
            {/* <h1>{accommodation.error}</h1> */}
            { accommodation.error == null ? '': <div className="field-error">    3 </div>}
          </div>
          <p className="accommodation__block__features-title"></p>
          <div className={"accommodation__block__features " + classDisabled}>
            {
              typeAmenities.map (({id, name, svg}) => {
                return <CheckboxBlack count={index} title={name} id={id} svg={svg} type={"checkbox"} handleChange={(e)=>this.handleChangeHuntList(e,index)} selectedOptions={accommodation.options} />
              })
            }
            
          </div>
          {this.validator.message('3', accommodation.options.join(''), 'required')}
          <div className={"accommodation__block__switch " + classDisabled}>
            <div className="accommodation__block__switch__column">
              <p>   </p>
              <span>//</span>
            </div>
            <Switch selectedOption={accommodation.drinks}
          handleChange={()=>this.handleCheckBox(index,'drinks')}/>
          </div>
          <div className={"accommodation__block__switch " + classDisabled}>
            <p>  </p>
            <Switch selectedOption={accommodation.has_animal}
          handleChange={()=>this.handleCheckBox(index,'has_animal')} />
          </div>
          <div className={"accommodation__block__textarea " + classDisabled}>
            <Textarea placeholder="" handleChange={(e)=> this.handleTextarea(e,index)} value={accommodation.animal_description}/>
          </div>
            <div className={"accommodation__block-delete " + classDisabled} onClick={() =>this.deleteAccommodation(index)}>  </div>
      </div>
      </div>
        })
        return (
          <div className="accommodation">
            <h1 className="accommodation-title"></h1>
            {accommodations}
            <div className={"accommodation__add " + classDisabled } onClick={this.addAccommodation}>
              <div className="accommodation__add-icon">
                <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" viewBox="0 0 16 16"><g><g><path fill="#242323" d="M7 0h2v7h7v2H9v7H7V9H0V7h7z"/></g></g></svg>
              </div>
              <div className="accommodation__add-text" >  </div>
            </div>
            <div className="accommodation__btn">
              <Button title="" type="primary" action={this.prevStep}/>
              <Button title="" type="primary" action={this.nextStep}/>
            </div>
        </div>
        )
  }
}

const mapStateToProps = ({typeAmenities,step,typeAccommodation,accomodation_step}) => {
  return {
    typeAmenities,
    typeAccommodation,
    step,
    accomodation_step
  }
}
export default connect(mapStateToProps,{accomadationStep,stepLoaded,filterAcomodation,
  updateFilterAcc})(Accommodation)