import React, {Component} from 'react'
import {connect} from 'react-redux'
import SimpleReactValidator from 'simple-react-validator';

import Dropdown from '../../form/dropdown'
import Input from '../../form/input'
import Button from '../../form/button'

import axios from 'axios'

import './food.scss'

import  {foodStep,stepLoaded,  filterFood,
  updateFilterFood} from '../../../actions'
import AuthServices from '../../../services/AuthServices';

class Food extends Component {
  state = {
    disabledAll: false,
    arrayFoods: [
      {
        id: 0,
        foodId: '',
        firstTitle: '  ',
        foodPrice: ''
      }
    ],
  }  

  componentWillMount() {
    this.validator = new SimpleReactValidator({  messages: {
      required: '    ',
      min: '    ',
    }})
  }

  componentDidMount () {
    const {food_step} = this.props
    if (food_step.length >0) {
      this.setState({
        arrayFoods:food_step
      })
    }
  }
  handleInput = (e, cost, i) => {
    let value = e.target.value;

    let stateCopy = Object.assign({}, this.state);
    stateCopy.arrayFoods = stateCopy.arrayFoods.slice();
    stateCopy.arrayFoods[i]= Object.assign({}, stateCopy.arrayFoods[i]);
    stateCopy.arrayFoods[i].foodPrice = value;
    this.setState(stateCopy)
  };
  nextStep = () => {
    const {foodStep,stepLoaded,} = this.props
    const {arrayFoods,disabledAll} = this.state
    if (disabledAll) {
      this.createProp()
      foodStep(arrayFoods)
      stepLoaded(8)
    }
    if (this.validator.allValid()) {
      // 
      this.createProp()
      foodStep(arrayFoods)
      stepLoaded(8)
    } else {
      this.validator.showMessages();
      this.forceUpdate();
    }

   
  };

  createProp = async() => {
    axios.defaults.headers.common['x-api-token'] = 'Bearer ' + localStorage.token 
    const {arrayFoods} = this.state
    const {    details_title,
      details_lat,
      details_lng,
      details_cnt_hunter_min,
      details_cnt_hunter_max,
      details_cnt_day_min,
      details_cnt_day_max,auto_details,train_details,plain_details,        equipment,
      additional_things,
      short_desrtiption,details_attachments_id, animals_step,accomodation_step} = this.props
      console.log(auto_details)
    const res = await AuthServices.createProposal({details_title,
      details_lat,
      details_lng,
      details_cnt_hunter_min,
      details_cnt_hunter_max,
      details_cnt_day_min,
      details_cnt_day_max,auto_details,train_details,plain_details, equipment,
      additional_things,
      short_desrtiption,arrayFoods,details_attachments_id ,animals_step,accomodation_step})
    console.log(res)
  }
  prevStep = () => {
    const {foodStep,stepLoaded} = this.props
    const {arrayFoods} = this.state
    stepLoaded(6);
    foodStep(arrayFoods)
  };
  deleteFood = (i) => {
    const {arrayFoods} = this.state
    if(arrayFoods.length > 1) {
      this.setState((state) => {
        const list = state.arrayFoods.filter((item, j) => i !== j);
        return {
          arrayFoods:list
        }
      })
    }
  }
  addFood = () => {
    console.log('qwe')
    this.setState(state => {
      let food = {
        id: state.arrayFoods.length + 1,
        foodId: '',
        firstTitle: '  ',
        foodPrice: ''
      }
      const list = state.arrayFoods.concat(food);

      return {
        arrayFoods:list
      };
    });
  };
  chooseName =({id,name},i) => {
    let stateCopy = Object.assign({}, this.state);
    stateCopy.arrayFoods = stateCopy.arrayFoods.slice();
    stateCopy.arrayFoods[i]= Object.assign({}, stateCopy.arrayFoods[i]);
    stateCopy.arrayFoods[i].foodId = id;
    stateCopy.arrayFoods[i].firstTitle = name;
    this.setState(stateCopy)
    if(name === '') {
      this.setState({
        disabledAll: true
      })
    } else {
      this.setState({
        disabledAll: false
      })
    }
  }
  search = (e)=>{
    
    const {filterFood
     } = this.props
    const value = e.target.value
    filterFood(value)
  }
  updateFilter = () => {
    const { updateFilterFood} =this.props
    updateFilterFood()
  }
  render () {
    const {arrayFoods, disabledAll} = this.state
    const {typeFood} = this.props
    let classDisabled= ''
    if (disabledAll) {
      classDisabled = 'disabled-block'
    }
    const foods = arrayFoods.map((food, index) => {
      return <section className="food">
              <div className="food-list">
                <Dropdown title={' '} list={typeFood}  firstTitle={food.firstTitle}  handleSearch={this.search} updateFilter={this.updateFilter} giveName={(item)=>this.chooseName(item,index)} error={this.validator.message('1123', food.foodId, 'required')}/>
                <div className={classDisabled}>
                <Input
                  title="  "
                  inputType="text"
                  name="  "
                  mask={[ '', ' ',/[1-9]/, /\d/,  /\d/, /\d/,  /\d/, /\d/, /\d/, /\d/]}
                  value={food.foodPrice}
                  placeholder={""}
                  handleChange={(e) =>this.handleInput(e, food.foodPrice, index)}
                  error={this.validator.message('2', food.foodPrice, 'required|min:3')}
                /> 
                </div>

              </div>
              <div className={"food-delete " + classDisabled } onClick={() =>this.deleteFood(index)}>  </div>
            </section>
    })
    return (
      <div className="food-wrapper">
        <div className="title"></div>
        {foods}
        <div className={"food-add " + classDisabled} onClick={this.addFood}>
          <div className="food-add__icon">
            <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" viewBox="0 0 16 16"><g><g><path fill="#242323" d="M7 0h2v7h7v2H9v7H7V9H0V7h7z"/></g></g></svg>
          </div>
          <div className="food-add__text" >  </div>
        </div>
        <div className="food-btn">
          <Button title="" type="primary" action={this.prevStep}/>
          <Button title="" type="primary" action={this.nextStep}/>
        </div>
      </div>
    )
  }
}

const mapStateToProps = ({typeFood,food_step,details_title,
  details_lat,
  details_lng,
  details_cnt_hunter_min,
  details_cnt_hunter_max,
  details_cnt_day_min,
  details_cnt_day_max,
  details_attachments,
  auto_details,train_details,plain_details,        equipment,
  additional_things,
  short_desrtiption,details_attachments_id,animals_step,accomodation_step}) => {
  return {
    typeFood,
    food_step,
    details_title,
    details_lat,
    details_lng,
    details_cnt_hunter_min,
    details_cnt_hunter_max,
    details_cnt_day_min,
    details_cnt_day_max,
    details_attachments,
    auto_details,train_details,plain_details,        equipment,
    additional_things,
    short_desrtiption,
    details_attachments_id,
    animals_step,
    accomodation_step
  }
}
export default connect(mapStateToProps, {foodStep,stepLoaded,  filterFood,
  updateFilterFood})(Food)