import React from 'react'
import './count.scss'

const Count = ({title, name, label, counter, add, remove, counterMax,error}) => {
    let description;
    if (title) {
      description = <div className="wrap-title">{title}</div>
    };
  return (
    <div className="wrap">
      {description}
      <label htmlFor={name} className="wrap-label">{label}</label>
      <div className="wrap-count">
        <button className="wrap-count-button wrap-count-minus" onClick={remove} disabled={counter === 1}>
          <svg id="SVGDoc" width="16" height="2" viewBox="0 0 16 2"><g><g><path d="M16,0v0v2v0h-16v0v-2v0z" fill="#242323"></path></g></g></svg>
        </button>
      <div>
        {counter}
      </div>
        <button className="wrap-count-button wrap-count-add" onClick={add} disabled={counter == counterMax}>
          <svg id="SVGDoc" width="16" height="16" viewBox="0 0 16 16"><g><g><path d="M7,0h2v7l7,0v2l-7,0v7h-2l0,-7l-7,0v-2l7,0z" fill="#242323"></path></g></g></svg>
        </button>
      </div>
    </div>
  )
}

export default Count