import React from 'react'

import './image-loader.scss'

const ImageLoader = ({handle,previewImage, closeImage, name, id, title, active,clickHandleImage}) => {
    let preview = ''
    let closeBtn = ''
    let labelClass = 'loader-label'
    let titleComp;
    if(title) titleComp = <div className="loader-title">{title}</div>
    if (previewImage !== null ) {
      preview = <img className="loader-image" src={previewImage} />
      closeBtn = <div className="loader-close" onClick={closeImage}>
        <svg xmlns="http://www.w3.org/2000/svg" width="8" height="8" viewBox="0 0 8 8"><g><g><path fill="#242323" d="M4.727 4L8 7.273 7.273 8 4 4.727.727 8 0 7.273 3.273 4 0 .727.727 0 4 3.273 7.273 0 8 .727z" /></g></g></svg>
      </div>
      labelClass += ' loader-label--border'
    }
    if (active) {
      return (
        <div className="loader">
          {titleComp}
          <input type="file" name={name} id={id} className="loader-input" onChange={handle} onClick={clickHandleImage} />
          <label htmlFor={id} className={labelClass}>
            <span>
              <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" viewBox="0 0 16 16"><g><g><path fill="#242323" d="M7 0h2v7h7v2H9v7H7V9H0V7h7z"/></g></g></svg>
            </span>
          </label>
          <div>
            {preview}
            {closeBtn}
          </div>
          
        </div>
      )
    } else {
      return (
        <label className='loader-label'>

        </label>
      )
    }

}

export default ImageLoader