import React, { Component } from "react";
import {connect} from 'react-redux';
import axios from 'axios'
import AuthServices from '../../../services/AuthServices'
import SimpleReactValidator from 'simple-react-validator';

import Input from "../../form/input";
import InputApi from "../../form/input-api";
import ImageLoaderList from "../../form/image-loader-list";
import Checkbox from '../../form/checkbox'
import Button from "../../form/button";
import {fethSingUp,stepLoaded} from '../../../actions'
import './personal-info.scss'


class PersonalInfo extends Component {

  state = {
    ambasadorChecked: true,
    newUser: {
      surname: "",
      name: "",
      patronymic: "",
      birthday: "",
      birthdayError: null,
      city: "",
      city_lat: Number,
      city_lng: Number,
      email: "",
      emailError: null,
      phoneNumber: "",
      phoneNumberError: null,
      pasport: '',
      ticket: '',
      datePasport: '',
      datePasportError: null,
      dateTicket: '',
      dateTicketError: null,
      pasportPhoto: [],
      ticketPhoto: [],
      ambasadorName: '',
      ambasadorSurname: '',
      ambasadorError: null,
      ambasadorId: null,
      // pasportPhotoFile: null,
      code: '',
      country_code: 'RU',
      pasportPhotoFile: [
        {
          id: 0,
          name: 'image',
          privew: null,
          active: true
        },
        {
          id: 1,
          name: 'image1',
          privew: null,
          active: false
        },
        {
          id: 2,
          name: 'image2',
          privew: null,
          active: false
        }
      ],
      ticketPhotoFile: [
        {
          id: 0,
          name: 'image',
          privew: null,
          active: true
        },
        {
          id: 1,
          name: 'image1',
          privew: null,
          active: false
        },
        {
          id: 2,
          name: 'image2',
          privew: null,
          active: false
        }
      ],
    },
    search: '',
    value: ''
  };
  
  componentWillMount() {
    this.validator = new SimpleReactValidator({  messages: {
      email: ' .    ',
      required: '    ',
      // OR
      default: 'Validation has failed!'  // will override all messages
    }})
  }
  
  handleInput= (e, name) => {
    let value = e.target.value;
    if (name === 'surname' || name === 'patronymic' || name === 'name') {
      value = value.replace(/[^a-z- ]/ig, '')
      if (value) {
        value = value[0].toUpperCase() + value.slice(1)
      }
    }

    let stateCopy = Object.assign({}, this.state);
    stateCopy.newUser[name] = value;
    this.setState(stateCopy, this.handleValidation);
  };


  handleChangePasportPhoto = (e,i) => {
    let file = e.target.files
    if( file.length !== 0 && (file[0].type == 'image/png' || file[0].type == 'image/jpeg')) {
      console.log(file[0])
      let reader = new FileReader()
      reader.readAsDataURL(file[0])
      console.log(file[0])
      setTimeout(() => {
        console.log(file)
        let stateCopy = Object.assign({}, this.state);
        stateCopy.newUser.pasportPhoto = stateCopy.newUser.pasportPhoto.concat(file[0])
        stateCopy.newUser.pasportPhotoFile[i].privew = reader.result;
        if(stateCopy.newUser.pasportPhotoFile[i+1] != undefined)  stateCopy.newUser.pasportPhotoFile[i+1].active = true;
        this.setState(stateCopy)
      }, 500)
    }
    e.preventDefault();
  }
  clickHandlePasport = (e) =>{
    e.target.value = null
  }
  closePasportPhoto = (e,i) =>{
    let stateCopy = Object.assign({}, this.state);
    let list = this.state.newUser.pasportPhotoFile.filter((item, j) => i !== j);
    let photoList = this.state.newUser.pasportPhoto.filter((item, j) => i !== j)
    const image = {
      id: i,
      name: `image${i}`,
      privew: null,
      active: this.state.newUser.pasportPhoto.length == 3 ? true:false
    }
    let newList = list.concat(image)
    stateCopy.newUser.pasportPhotoFile = newList;
    stateCopy.newUser.pasportPhoto = photoList;
    this.setState(stateCopy)
  }
  handleChangeTicketPhoto = (e,i) => {
    let file = e.target.files
    if( file.length !== 0) {
      console.log(file[0])
      let reader = new FileReader()
      reader.readAsDataURL(file[0])

      setTimeout(() => {
        console.log(file)
        let stateCopy = Object.assign({}, this.state);
        stateCopy.newUser.ticketPhoto = stateCopy.newUser.ticketPhoto.concat(file[0])
        stateCopy.newUser.ticketPhotoFile[i].privew = reader.result;
        if(stateCopy.newUser.ticketPhotoFile[i+1] != undefined)  stateCopy.newUser.ticketPhotoFile[i+1].active = true;
        this.setState(stateCopy)
      }, 500)
    }
    e.preventDefault();
  }
  clickHandleTicket = (e) =>{
    e.target.value = null
  }
  closeTicketPhoto = (e,i) =>{
    let stateCopy = Object.assign({}, this.state);
    let list = this.state.newUser.ticketPhotoFile.filter((item, j) => i !== j);
    let photoList = this.state.newUser.ticketPhoto.filter((item, j) => i !== j)
    const image = {
      id: i,
      name: `image${i}`,
      privew: null,
      active: this.state.newUser.ticketPhoto.length == 3 ? true:false
    }
    let newList = list.concat(image)
    stateCopy.newUser.ticketPhotoFile = newList;
    stateCopy.newUser.ticketPhoto = photoList;
    this.setState(stateCopy)
  }

  nextStep = () => {
    const{newUser,validatio,ambasadorChecked}= this.state
    const {fethSingUp,stepLoaded} = this.props
    if (this.validator.allValid() && (ambasadorChecked || newUser.ambasadorName.length >0)) {
      fethSingUp(newUser).then((res)=>{
        console.log(res)
        console.log(res.status)
        if (res.status == 200) {
          console.log(res.status)
          axios.defaults.headers.common['x-api-token'] = 'Bearer ' + localStorage.token 
          if (newUser.pasportPhoto.length !== 0) {
            newUser.pasportPhoto.forEach((file) => {
              this.uploadPhotos('passport',file)
            })
          }
          if (newUser.ticketPhoto.length !== 0) {
            newUser.ticketPhoto.forEach((file) => {
              this.uploadPhotos('hunterTicket',file)
            })
          } 
          stepLoaded(2)
        } else if (res.status == 400) {
          if (res.data.error) {
            if(res.data.error.errors.date_born) {
              if (res.data.error.errors && res.data.error.errors.date_born[0]) {
                this.setState(prevState=> {
                  return {
                    newUser: {
                      ...prevState.newUser,
                      birthdayError: res.data.error.errors.date_born[0]
                    }
                  }
                })
              }
            } else {
              this.setState(prevState=> {
                return {
                  newUser: {
                    ...prevState.newUser,
                    birthdayError: null
                  }
                } 
              }) 
            }
            if(res.data.error.errors.phone_number) {
              if (res.data.error.errors && res.data.error.errors.phone_number[0]) {
                this.setState(prevState=> {
                  return {
                    newUser: {
                      ...prevState.newUser,
                      phoneNumberError: res.data.error.errors.phone_number[0]
                    }
                  }
                })
              }
            } else {
              this.setState(prevState=> {
                return {
                  newUser: {
                    ...prevState.newUser,
                    phoneNumberError: null
                  }
                } 
              }) 
            }
            if(res.data.error.errors.email) {
              if (res.data.error.errors && res.data.error.errors.email[0]) {
                this.setState(prevState=> {
                  return {
                    newUser: {
                      ...prevState.newUser,
                      emailError: res.data.error.errors.email[0]
                    }
                  }
                })
              }
            } else {
              this.setState(prevState=> {
                return {
                  newUser: {
                    ...prevState.newUser,
                    emailError: null
                  }
                }
              })
            }

            if (res.data.error.errors && res.data.error.errors.hunter_ticket_date) {
              this.setState(prevState=> {
                return {
                  newUser: {
                    ...prevState.newUser,
                    dateTicketError: res.data.error.errors.hunter_ticket_date
                  }
                }
              })
            } else {
              this.setState(prevState=> {
                return {
                  newUser: {
                    ...prevState.newUser,
                    dateTicketError: null
                  }
                }
              })
            }
            if (res.data.error.errors && res.data.error.errors.passport_date) {
              this.setState(prevState=> {
                return {
                  newUser: {
                    ...prevState.newUser,
                    datePasportError: res.data.error.errors.passport_date
                  }
                }
              })
            } else {
              this.setState(prevState=> {
                return {
                  newUser: {
                    ...prevState.newUser,
                    datePasportError: null
                  }
                }
              })    
            }
          }

        }

      
      })
      
    } else {
      this.validator.showMessages();
      this.forceUpdate();
    } 
  }

  uploadPhotos = async(name,photo) => {
    const res = AuthServices.uploadPhoto({fileType: name, file: photo})
    console.log(res)
  }
  getAddress = (body) => {
    console.log(body)
    let code;
    body.address_components.forEach((item, i)=>{
      if (item.short_name.length == 2) {
        code = item.short_name
      }
    })
    this.setState(
      prevState => ({
        newUser: {
          ...prevState.newUser,
          city: body.formatted_address,
          city_lat: body.geometry.location.lat(),
          city_lng:body.geometry.location.lng(),
          country_code: code
        }
      })
    )
    console.log(body.geometry.location.lat())
    console.log(body.geometry.location.lng())
  }
  handleInputChange = (e) =>{
    this.setState({search: e.target.value, value: e.target.value})
  }

  handleSelectSuggest = (suggest) => {
    this.getAddress(suggest)
    this.setState({search: "", value: suggest.formatted_address})
  }
  // checkAmb = () => {
  //   if(this.state.ambasadorChecked == true) {
  //     let stateCopy = Object.assign({}, this.state);
  //     stateCopy.newUser.ambasadorName = 'qwqeqwe';
  //     this.setState(stateCopy);
  //   }else  {
  //     let stateCopy = Object.assign({}, this.state);
  //     stateCopy.newUser.ambasadorName = '';
  //     this.setState(stateCopy);
  //   }
  // } 
  handleCheckbox = () =>{
    this.setState((state)=>{
      return {
        ambasadorChecked: !state.ambasadorChecked
      }
    })
  }
  handleKeyPress= async(e) => {
    let value = e.target.value
    // if(value.length > 6){
      console.log(value)
      try {
        const res = await AuthServices.getAmbasador(value)
        console.log(res)
        let stateCopy = Object.assign({}, this.state);

        stateCopy.newUser.ambasadorName = res.data.first_name
        stateCopy.newUser.ambasadorSurname = res.data.last_name
        stateCopy.newUser.ambasadorId = res.data.id
        stateCopy.newUser.ambasadorError = null
        this.setState(stateCopy)
      } catch(e) {
        console.log(e.response)
        let stateCopy = Object.assign({}, this.state);
        stateCopy.newUser.ambasadorName = ''
        stateCopy.newUser.ambasadorSurname = ''
        stateCopy.newUser.ambasadorError = e.response.data.error.message
        this.setState(stateCopy)
      }

    // }

    
  }
  render() {
    const {newUser, validation, search, value, ambasadorChecked} = this.state

    return (
      <div className="info-wrapper">
        <div className="title">  </div>
        <section className="info">
          <div>
          <Input
            inputType="text"
            mask={null}
            name=""
            value={newUser.surname}
            placeholder={""}
            handleChange={(e) =>this.handleInput(e,'surname')}
            error={this.validator.message('1', newUser.surname, 'required')}
          />
          </div>
          <Input
            inputType="text"
            name=""
            mask={null}
            value={newUser.name}
            placeholder={""}
            handleChange={(e) =>this.handleInput(e,'name')}
            error={this.validator.message('2', newUser.name, 'required')}
          />
          <Input
            inputType="text"
            name=""
            mask={null}
            value={newUser.patronymic}
            placeholder={""}
            handleChange={(e) =>this.handleInput(e,'patronymic')}
            error={this.validator.message('3', newUser.patronymic, 'required')}
          />
          <div className="info-list">
            <Input
              inputType="text"
              name=" "
              mask={[  /[1-9]/, /\d/, '/', /\d/, /\d/,  '/', /\d/, /\d/, /\d/, /\d/]}
              value={newUser.birthday}
              placeholder={" "}
              handleChange={(e) =>this.handleInput(e,'birthday')}
              error={this.validator.message('4', newUser.birthday, 'required') || newUser.birthdayError}
            /> 
            <InputApi
              inputType="text"
              name=""
              placeholder={""}
              handleChange={this.handleInputChange} handleSuggest={this.handleSelectSuggest}  search={search} value={value}
              error={this.validator.message('5', value, 'required')}
            /> 
            
          </div>
          <div className="info-list info-list--half">
            <Input
              inputType="text"
              name="E-mail"
              mask={null}
              value={newUser.email}
              placeholder={"E-mail"}
              handleChange={(e) =>this.handleInput(e,'email')}
              error={this.validator.message('6', newUser.email, 'required|email') || newUser.emailError}
            /> 
            <Input
              inputType="text"
              name=" "
              mask={[ '+', /[1-9]/ ,'(', /[1-9]/ , /\d/, /\d/, ')', ' ', /\d/, /\d/, /\d/, '-', /\d/, /\d/, '-', /\d/, /\d/,/\d/,/\d/]}
              value={newUser.phoneNumber}
              placeholder={" "}
              handleChange={(e) =>this.handleInput(e,'phoneNumber')}
              error={this.validator.message('7', newUser.phoneNumber, 'required') || newUser.phoneNumberError}
            />        
          </div>
          <div className="info-pasport">
            <div className="info-list info-list--half">
            <div>
              <Input
                  title=""
                  inputType="text"
                  mask={null}
                  name="   "
                  value={newUser.pasport}
                  placeholder={"   "}
                  max={14}
                  handleChange={(e) =>this.handleInput(e,'pasport')}
                  error={this.validator.message('8', newUser.pasport, 'required')}
                /> 
              <div className="info-list__item">
                <Input
                    inputType="text"
                    name=" "
                    mask={[  /[1-9]/, /\d/, '/', /\d/, /\d/,  '/', /\d/, /\d/, /\d/, /\d/]}
                    value={newUser.datePasport}
                    placeholder={" "}
                    handleChange={(e) =>this.handleInput(e,'datePasport')}
                    error={this.validator.message('9', newUser.datePasport, 'required') || newUser.datePasportError}
                  /> 
              </div> 
              <ImageLoaderList images={newUser.pasportPhotoFile} handle={(e,i)=>this.handleChangePasportPhoto(e,i)} close={(e,i)=>this.closePasportPhoto(e,i)} id={'pasport'} clickHandle={this.clickHandlePasport}/>

              {/* <ImageLoader active={true} title={' '} name={'pasport'} id={'pasport'}  previewImage={newUser.ticketPhoto} handle={this.handleChangeTicketPhoto} closeImage={this.closeTicketPhoto}/> */}
            </div>
            <div>
              <Input
                title=" "
                inputType="text"
                mask={null}
                name="   "
                value={newUser.ticket}
                placeholder={"   "}
                max={14}
                handleChange={(e) =>this.handleInput(e,'ticket')}
                error={this.validator.message('10', newUser.ticket, 'required')}
              /> 
              <div className="info-list__item">
                <Input
                  inputType="text"
                  name=" "
                  mask={[  /[1-9]/, /\d/, '/', /\d/, /\d/,  '/', /\d/, /\d/, /\d/, /\d/]}
                  value={newUser.dateTicket}
                  placeholder={" "}
                  handleChange={(e) =>this.handleInput(e,'dateTicket')}
                  error={this.validator.message('11', newUser.dateTicket, 'required') || newUser.dateTicketError}
                />        
              </div>
              {/* <ImageLoader active={true} title={' '}  name={'ticket'} id={'ticket'}   previewImage={newUser.pasportPhoto} handle={this.handleChangePasportPhoto} closeImage={this.closePasportPhoto}/>   */}
              
              <ImageLoaderList images={newUser.ticketPhotoFile} handle={(e,i)=>this.handleChangeTicketPhoto(e,i)} close={(e,i)=>this.closeTicketPhoto(e,i)} id={'ticket'} clickHandle={this.clickHandleTicket}/>
              {/* <ImageLoaderList images={newUser.pasportPhotoFile} handle={(e,i)=>this.handleChangePasportPhoto(e,i)} close={(e,i)=>this.closePasportPhoto(e,i)} clickHandle={this.clickHandlePasport}/> */}
              </div>
            </div>
            <div className="info-ambasador">
              <div className="info-ambasador__title"> </div>
              <Checkbox title={"   "} handle={this.handleCheckbox} value={ambasadorChecked}/>
              {!ambasadorChecked ? <div className="info-list info-list--half info-list--ambasador">
                <div>
                  <Input
                    inputType="text"
                    name=" "
                    mask={null}
                    // value={newUser.code}
                    placeholder={" "}
                    // handleChange={(e) =>this.handleInput(e,'code')}
                    handleChange={this.handleKeyPress}
                    error={newUser.ambasadorError}
                  />  
                </div>
                <div className="info-ambasador__text">
                  <div className="info-ambasador__text-title"></div>
                  <div className="info-ambasador__text-name">{newUser.ambasadorName + ' ' + newUser.ambasadorSurname}</div>
                </div>
                {  !ambasadorChecked && newUser.ambasadorName.length == 0 ?<div className="srv-validation-message">     </div>:''}
              </div> : ''}

            </div>
            <div className="info-btn">
              <Button title="" type="primary" action={this.nextStep}/>
            </div>
            <div className="info-politic">
                ,          &nbsp;
              <span className="info-politic--red">
                 
              </span>
            </div>
          </div>
        </section>
      </div>
    );
  }
}


export default connect(null, {stepLoaded, fethSingUp})(PersonalInfo);
