import React, { Component } from "react";
import { connect } from "react-redux";
import axios from 'axios'
import SimpleReactValidator from 'simple-react-validator';
import AuthServices from '../../../services/AuthServices'

import Input from "../../form/input";
import InputApi from "../../form/input-api";
import Button from "../../form/button";
import Count from "../../form/count";
import ImageLoaderList from "../../form/image-loader-list";
import { stepLoaded, detailsObject } from "../../../actions";
import "./hunting-details.scss";


class HuntingDetails extends Component {
  state = {
    error: false,
    hunterError: null,
    daysError: null,
    details: {
      title: "",
      p_lat: Number,
      p_lng: Number
    },
    hunters: {
      min: 1,
      max: 1
    },
    days: {
      min: 1,
      max: 1
    },
    val: '',
    sear: '',
    attachments: [],
    images: [
      {
        id: 0,
        name: 'image',
        privew: null,
        active: true
      },
      {
        id: 1,
        name: 'image1',
        privew: null,
        active: false
      },
      {
        id: 2,
        name: 'image2',
        privew: null,
        active: false
      },
      {
        id: 3,
        name: 'image3',
        privew: null,
        active: false
      },
      {
        id: 4,
        name: 'image4',
        privew: null,
        active: false
      },
      {
        id: 5,
        name: 'image5',
        privew: null,
        active: false
      },
      {
        id: 6,
        name: 'image6',
        privew: null,
        active: false
      },
      {
        id: 7,
        name: 'image7',
        privew: null,
        active: false
      },
    ],
  };
  componentWillMount () {
    this.validator = new SimpleReactValidator({  messages: {
      required: '    ',
      min: '      :min:type',
      max: '      :max:type',
      default: 'Validation has failed!'  
    }})
  }
  componentDidMount() {
    const {
      details_title,
      details_lat,
      details_lng,
      details_cnt_hunter_min,
      details_cnt_hunter_max,
      details_cnt_day_min,
      details_cnt_day_max,
      details_attachments,
      details_attachments_id,
      details_address
    } = this.props;
    this.setState(prevState => ({
      details: {
        ...prevState.details,
        title: details_title,
        p_lat: details_lat,
        p_lng: details_lng
      },
      attachments: details_attachments_id,
      hunters: {
        ...prevState.hunters,
        min: details_cnt_hunter_min,
        max: details_cnt_hunter_max
      },
      days: {
        ...prevState.days,
        min: details_cnt_day_min,
        max: details_cnt_day_max
      },
      images: details_attachments,
      val: details_address
    }));

  }
  handleInputTitle = e => {
    let value = e.target.value;
    this.setState(prevState => ({
      details: {
        ...prevState.details,
        title: value
      }
    }));
  };
  handleInputPlace = e => {
    let value = e.target.value;
    this.setState(prevState => ({
      details: {
        ...prevState.details,
        place: value
      }
    }));
  };
  countError = () => {
    if (this.state.hunters.min > this.state.hunters.max) {
      this.setState({
        hunterError: '      '
      })
    } else {
      this.setState({
        hunterError: null
      })
    }
  }
  daysError = () => {
    if (this.state.days.min > this.state.days.max) {
      this.setState({
        daysError: '      '
      })
    } else {
      this.setState({
        daysError: null
      })
    }
  }
  addHuntersMin = () => {
    let count = this.state.hunters.min + 1;
    this.setState(prevState => ({
      hunters: {
        ...prevState.hunters,
        min: count
      }
    }),this.countError);

  };
  removeHuntersMin = () => {
    let count = this.state.hunters.min - 1;
    this.setState(prevState => ({
      hunters: {
        ...prevState.hunters,
        min: count
      }
    }),this.countError);

  };
  addHuntersMax = () => {
    let count = this.state.hunters.max + 1;
    this.setState(prevState => ({
      hunters: {
        ...prevState.hunters,
        max: count
      }
    }),this.countError);
  };
  removeHuntersMax = () => {
    let count = this.state.hunters.max - 1;
    this.setState(prevState => ({
      hunters: {
        ...prevState.hunters,
        max: count
      }
    }),this.countError);

  };
  addDaysMin = () => {
    let count = this.state.days.min + 1;
    this.setState(prevState => ({
      days: {
        ...prevState.days,
        min: count
      }
    }),this.daysError);
  };
  removeDaysMin = () => {
    let count = this.state.days.min - 1;
    this.setState(prevState => ({
      days: {
        ...prevState.days,
        min: count
      }
    }),this.daysError);
  };
  addDaysMax = () => {
    let count = this.state.days.max + 1;
    this.setState(prevState => ({
      days: {
        ...prevState.days,
        max: count
      }
    }),this.daysError);
  };
  removeDaysMax = () => {
    let count = this.state.days.max - 1;
    this.setState(prevState => ({
      days: {
        ...prevState.days,
        max: count
      }
    }),this.daysError);
  };
  getAddress = body => {
    this.setState(prevState => ({
      details: {
        ...prevState.details,
        p_lat: body.geometry.location.lat(),
        p_lng: body.geometry.location.lng()
      }
    }));
  };
  nextStep = () => {
    const { detailsObject, stepLoaded } = this.props;
    const { details, hunters, days, images, val,attachments, daysError ,hunterError} = this.state;

    detailsObject({
      title: details.title,
      p_lat: details.p_lat,
      p_lng: details.p_lng,
      cnt_hunter_min: hunters.min,
      cnt_hunter_max: hunters.max,
      cnt_day_min: days.min,
      cnt_day_max: days.max,
      images: images,
      attachments,
      address: val
    });
    if (this.validator.allValid() &&  daysError ==null && hunterError == null) {
       if (images[2].privew == null) {
         console.log('qwe')
        this.setState({error:true})
       } else  {
        console.log('qwe2')
        this.setState({error:false})
        stepLoaded(3)
       }
        
        
        
    } else {
      if (images[2].privew == null) {
        this.setState({error:true})
       } else {
        this.setState({error:false})
       }
      this.validator.showMessages();
      this.forceUpdate();
    }
    
  };
  prevStep = () => {    
  const { detailsObject, stepLoaded } = this.props;
  const { details, hunters, days, images, val} = this.state;

  detailsObject({
    title: details.title,
    p_lat: details.p_lat,
    p_lng: details.p_lng,
    cnt_hunter_min: hunters.min,
    cnt_hunter_max: hunters.max,
    cnt_day_min: days.min,
    cnt_day_max: days.max,
    images: images,
    address: val
  });
    stepLoaded(1);
  };
  showPhoto = (e, i) => {
    console.log(e.target.files)
    console.log(i)
    
    let file = e.target.files
    this.uploadPhotos('huntPhoto', file[0])
    if( file.length !== 0 && (file[0].type == 'image/png' || file[0].type == 'image/jpeg')) {
      console.log(file[0])
      let reader = new FileReader()
      reader.readAsDataURL(file[0])
      setTimeout(() => {
        let stateCopy = Object.assign({}, this.state);
        stateCopy.images = stateCopy.images.slice();
        stateCopy.images[i]= Object.assign({}, stateCopy.images[i]);
        stateCopy.images[i].privew = reader.result;
        i == 7 ? stateCopy.images[i].active = true: stateCopy.images[i+1].active = true;
        this.setState(stateCopy)
      }, 500)
    }
  }
  uploadPhotos = async(name,photo) => {
    axios.defaults.headers.common['x-api-token'] = 'Bearer ' + localStorage.token 
    const res = await AuthServices.uploadPhoto({fileType: name, file: photo})
    console.log(res)
    this.setState(prevState => ({
      attachments: [...prevState.attachments, res.data]
    }))
  }
  clickHandle = (e)=>{
    e.target.value = null
  }
  closeImage = (e, i) =>{
    console.log(i)
    this.setState(state => {
      let list = state.images.filter((item, j) => i !== j);
      let listAttach = state.attachments.filter((item, j)=> i !== j)
      console.log(list)
      const image = {
        id: i,
        name: `image${i}`,
        privew: null,
        active: state.attachments.length == 8 ? true :false
      }
      let newList = list.concat(image)
      console.log( newList)
      return {
        images: newList,
        attachments:listAttach
      };
    })
  }
  handleInputChange = (e) =>{
    this.setState({sear: e.target.value, val: e.target.value})
  }

  handleSelectSuggest = (suggest) => {
    this.getAddress(suggest)
    this.setState({sear: "", val: suggest.formatted_address})
  }

  render() {
    const { hunters, days, val,sear,error ,hunterError, daysError} = this.state;
    let errorText;
    if ( error) {
      errorText = <div className="field-error">    3 </div>
    }
    return (
      <div className="hunting-details">
        <h1 className="hunting-details-title">  </h1>
        <Input
          title=" "
          inputType="text"
          name="title"
          count={true}
          mask={null}
          value={this.state.details.title}
          placeholder={""}
          max={70}
          handleChange={this.handleInputTitle}
          error={this.validator.message('review', this.state.details.title, 'required|min:5|max:70')}
        />
        
        <InputApi placeholder="" handleChange={this.handleInputChange} handleSuggest={this.handleSelectSuggest}  search={sear} value={val} error={this.validator.message('1', val, 'required')}/>
        <div className="hunting-details__count">
          <div className="hunting-details__count__item">
            <Count
              title=" "
              name="search"
              label=""
              counter={hunters.min}
              remove={this.removeHuntersMin}
              add={this.addHuntersMin}
            />
            <Count
              name="search"
              label=""
              counter={hunters.max}
              counterMax={99}
              remove={this.removeHuntersMax}
              add={this.addHuntersMax}
            />
          </div>
          <div className="hunting-details__count__item">
            <Count
              title=" "
              name="search"
              label=""
              counter={days.min}
              remove={this.removeDaysMin}
              add={this.addDaysMin}
            />
            <Count
              name="search"
              label=""
              counter={days.max}
              remove={this.removeDaysMax}
              add={this.addDaysMax}
            />
          </div>
        </div>
        <div className="hunting-details__error">
          <div className="error-text">{hunterError}</div>
          <div className="error-text error-text--right">{daysError}</div>
        </div>
        <div className="info-loader">
          <ImageLoaderList clickHandle={this.clickHandle} id={'hunter'} images={this.state.images} handle={this.showPhoto} close={this.closeImage} text={true}/>
          
        </div>
        {errorText}
        <div className="hunting-details-btn">
          {/* <Button title="" action={this.prevStep} /> */}
          <Button title="" type="primary" action={this.nextStep} />
        </div>
      </div>
    );
  }
}

const mapStateToProps = ({
  details_title,
  details_lat,
  details_lng,
  details_cnt_hunter_min,
  details_cnt_hunter_max,
  details_cnt_day_min,
  details_cnt_day_max,
  details_attachments,
  details_address,
  details_attachments_id
}) => {
  return {
    details_title,
    details_lat,
    details_lng,
    details_cnt_hunter_min,
    details_cnt_hunter_max,
    details_cnt_day_min,
    details_cnt_day_max,
    details_attachments,
    details_address,
    details_attachments_id
  };
};

export default connect(
  mapStateToProps,
  { stepLoaded, detailsObject }
)(HuntingDetails);
