import React, {Component} from 'react'
import {connect} from 'react-redux'
import SimpleReactValidator from 'simple-react-validator';

import TextArea from '../../form/textarea'
import Button from '../../form/button'

import './description.scss'

import  {descriptionStep,stepLoaded} from '../../../actions'


class Description extends Component{
  state = {
    equipment: '',
    additional_things: '',
    short_desrtiption: ''
  }
  componentWillMount () {
    this.validator = new SimpleReactValidator({  messages: {
      required: '    ',
      min: '      :min:type',
      max: '      :max:type',
      default: 'Validation has failed!'  
    }})
  }
  componentDidMount () {
    const { equipment,
      additional_things,
      short_desrtiption} = this.props
      this.setState({
        equipment,
      additional_things,
      short_desrtiption
      })
  }
  nextStep = () => {
    const { equipment,
      additional_things,
      short_desrtiption} = this.state
    const {stepLoaded,descriptionStep} = this.props
    descriptionStep({    equipment,
    additional_things,
    short_desrtiption})    
    if (this.validator.allValid()) {
      
      stepLoaded(6);
    } else {
      this.validator.showMessages();
      this.forceUpdate();
    }
  }
  prevStep = () => {
    this.props.stepLoaded(4)
  }
  handleTextarea = (e,name) => {
    let value = e.target.value
    this.setState({
      [name]: value 
    })
  }
  render () {
    const {equipment, additional_things, short_desrtiption} = this.state
    return (
      
      <div className='description'>
        <div className="title"></div>
        <TextArea title={' '} placeholder={' '} handleChange={(e)=> this.handleTextarea(e, 'equipment')} value={equipment} error={this.validator.message('1', equipment, 'required|min:5|max:480')} max={480}/>
        <TextArea title={'   '} placeholder={' '} handleChange={(e)=> this.handleTextarea(e, 'additional_things')} value={additional_things} error={this.validator.message('2', additional_things, 'required|min:5|max:480')} max={480}/>
        <TextArea title={' '} placeholder={' '} handleChange={(e)=> this.handleTextarea(e, 'short_desrtiption')} value={short_desrtiption} error={this.validator.message('3', short_desrtiption, 'required|min:5|max:480')} max={480}/>
        <div className="description-btn">
          <Button title="" action={this.prevStep}/>
          <Button title="" type="primary" action={this.nextStep}/>
        </div>
      </div>
    )
  }
}

const mapStateToProps = ({equipment,
  additional_things,
  short_desrtiption}) => {
  return {
    equipment,
additional_things,
short_desrtiption
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
    stepLoaded: (nextStep) => {
      dispatch({
        type: 'NEXT_STEP',
        payload: nextStep
      })
    }
  }
}

export default connect(mapStateToProps,{descriptionStep,stepLoaded})(Description)