const fethSingUp = (payload) => {
  return async dispatch => {
    try {
      console.log(payload)
      const res = await AuthServices.singUp(payload);
      console.log(res)
      localStorage.setItem('token', res.data.token)
      // const payload = res.data;
      dispatch({ type: 'GET_TOKEN', payload:res.data.token});
      return res
    } catch (error) {
      return error.response
    }
  }
}
const fetchGetDict = () => {
  return async dispatch => {
    try {
      console.log('qwe')
      const res = await DictServices.getDict();
      console.log(res)
      const payload = res.data;
      dispatch({ type: 'GET_DICT', payload });

    } catch (error) {

    }
  };
};

