import React from 'react'
import './checkbox-black.scss'

const CheckboxBlack = ({title, id, svg, type,handleChange,selectedOptions,count }) => {
  let svgComponent;
  let inputComponent;
  if (type === 'checkbox') {
    inputComponent =    <input id={title+count} type={type} className="checkbox-black__input" 
    onChange={handleChange}
    value={id}
    checked={selectedOptions.includes(id + '')}/>
  } else if (type === 'radio') {
    inputComponent =    <input id={title+count} type={type}  className="checkbox-black__input" 
    onChange={handleChange}
    value={id}
    checked={selectedOptions === id + ''}/> 
  }
  
  if (svg) {
    svgComponent = <span>
                    {svg}
                  </span>
  }
  return (
    <div className="checkbox-black">
   
    
      {inputComponent}
  
      <label htmlFor={title+count} className="checkbox-black__label">
      {svgComponent}
      {title}
      </label>
    </div>
  )
}


export default CheckboxBlack