import React from 'react'
import ImageLoader from '../image-loader'
import './image-loader-list.scss'


class  ImageLoaderList extends React.Component {
  render () {
    
    const {images,handle, close,clickHandle, text, id} = this.props
    let textComponent;
    if(text) {
      textComponent = <span> <p className="loader-list-title"> </p>
                         <p className="loader-list-subtitle"> 8 </p> </span> 
    }
    const array = images.map((image, i) => {
      return <ImageLoader active={image.active}  name={image.name + id} id={image.id + id} previewImage={image.privew} key={i} handle={(e)=>handle(e, i)} closeImage={(e) => close(e, i)} clickHandleImage={clickHandle}/>
    })
    return (
      <div>
        {textComponent}
      <div className="loader-list">
        {array}
      </div>
      </div>
    )
  }


}

export default ImageLoaderList