import Api from './Api';


const convertDate = (val) => {
  const date = val.replace(/\//g,'-')
  const day = date.split('-')[0]
  const month = date.split('-')[1]
  const year = date.split('-')[2]
  return year + '-' + month + '-' + day
}

const formatDate  = (d) => {
  let day = d.getDate();
  let currentDay = day >= 1 && day < 10? '0'+ day: day
  let monthIndex = d.getMonth() + 1;
  let month = monthIndex >= 10 ? monthIndex: `0${monthIndex}`
  let year = d.getFullYear();
  return year + '-' + month + '-' + currentDay
}

const convertPhone = (val) => {
  const date = val.replace(/\(/g,'').replace(/\)/g,'').replace(/\+/g,'').replace(/\-/g,'').replace(' ','')
  return date

}

