

const initialState = {
  step: 0,
  animals: [],
  typeAccommodationFilter:[],
  typeFoodFilter: [],
  animalsFilter: [],
  typeAmenities: [],
  typeMethodHunt: [],
  typeHunt: [],
  typeAccommodation: [],
  typeFood: [],
  typeGun: [],
  typeTransport: [],
  token: localStorage.getItem("token") || "",
  details_title: "",
  details_address: '',
  details_lat: Number,
  details_lng: Number,
  details_cnt_hunter_min: 1,
  details_cnt_hunter_max: 1,
  details_cnt_day_min: 1,
  details_cnt_day_max: 1,
  details_attachments_id: [],
  details_attachments: [
    {
      id: 0,
      name: "image",
      privew: null,
      active: true
    },
    {
      id: 1,
      name: "image1",
      privew: null,
      active: false
    },
    {
      id: 2,
      name: "image2",
      privew: null,
      active: false
    },
    {
      id: 3,
      name: "image3",
      privew: null,
      active: false
    },
    {
      id: 4,
      name: "image4",
      privew: null,
      active: false
    },
    {
      id: 5,
      name: "image5",
      privew: null,
      active: false
    },
    {
      id: 6,
      name: "image6",
      privew: null,
      active: false
    },
    {
      id: 7,
      name: "image7",
      privew: null,
      active: false
    }
  ],
  auto_details: {},
  train_details: {},
  plain_details: {},
  animals_step: [],
  equipment: '',
  additional_things: '',
  short_desrtiption: '',
  accomodation_step: [],
  food_step: []
};


const reducer = (state = initialState, action) => {
  switch (action.type) {
    case 'FOOD_STEP':
      return {
        ...state,
        food_step: action.payload
      }
    case 'ACCOMADATION_STEP':
      return {
        ...state,
        accomodation_step: action.payload
      }
    case 'DECRIPTION_STEP':
      const {  equipment,
      additional_things,
      short_desrtiption} = action.payload
      return {
        ...state,
        equipment,
        additional_things,
        short_desrtiption
      }
    case "ANIMALS_STEP":
        console.log(action.payload)
      return {
        ...state,
        animals_step: action.payload
      }
    case "UPDATE_ANIMALS":
      return {
        ...state,
        animals: state.animalsFilter
      }
    case "UPDATE_ACC":
      return {
        ...state,
        typeAccommodation: state.typeAccommodationFilter
      }
    case "UPDATE_FOOD":
        return {
          ...state,
          typeFood: state.typeFoodFilter
        }
    case "TRAVEL":
      console.log(action.payload)
      return {
        ...state,
        auto_details: action.payload[0],
        train_details: action.payload[1],
        plain_details: action.payload[2],
      }
    case "DETAILS":
      const {title,p_lat,p_lng,cnt_hunter_min,cnt_hunter_max,cnt_day_min,cnt_day_max,images,address,attachments} = action.payload
      return {
        ...state,
        details_title: title,
        details_lat: p_lat,
        details_lng: p_lng,
        details_cnt_hunter_min: cnt_hunter_min,
        details_cnt_hunter_max: cnt_hunter_max,
        details_cnt_day_min: cnt_day_min,
        details_cnt_day_max: cnt_day_max,
        details_attachments: images,
        details_address: address,
        details_attachments_id: attachments
      };
    case "GET_TOKEN":
      return {
        ...state,
        token: action.payload
      };
    case "NEXT_STEP":
      return {
        ...state,
        step: action.payload
      };
    case "FILTER_LIST":
      const animals = state.animalsFilter.filter(item => {
        return item.name.toLowerCase().includes(action.payload);
      });
      return {
        ...state,
        animals
      };
    case "FILTER_LIST_ACCOMODATION":
      const accomodation = state.typeAccommodationFilter.filter(item => {
        return item.name.toLowerCase().includes(action.payload);
      });
      return {
        ...state,
        typeAccommodation: accomodation
      };
    case 'FILTER_LIST_FOOD':
      const food = state.typeFoodFilter.filter(item => {
        return item.name.toLowerCase().includes(action.payload);
      });
      return {
        ...state,
        typeFood: food
      };
    case 'METHODS_HUNT':
      return {
        ...state,
        typeMethodHunt: action.payload
      }  
    case "GET_DICT":
      console.log(action.payload);
      const typeAmenities = action.payload.types.filter(item => {
        return item.service_type === "typeAmenities";
      });
      const typeMethodHunt = action.payload.types.filter(item => {
        return item.service_type === "typeMethodHunt";
      });
      const typeHunt = action.payload.types.filter(item => {
        return item.service_type === "typeHunt";
      });
      const typeAccommodation = action.payload.types.filter(item => {
        return item.service_type === "typeAccommodation";
      });
      const typeFood = action.payload.types.filter(item => {
        return item.service_type === "typeFood";
      });
      const typeGun = action.payload.types.filter(item => {
        return item.service_type === "typeGun";
      });
      const typeTransport = action.payload.types.filter(item => {
        return item.service_type === "typeTransport";
      });

      let newArray = action.payload.animals.map(item => {
        return item.childrens.map((animal)=>{
           return {
             id: animal.id,
             name: animal.name,
             methods: item.methodHunts
           }
         })
      });
      let merged = [].concat.apply([], newArray);
      return {
        ...state,
        typeAmenities,
        typeMethodHunt,
        typeHunt,
        typeAccommodation,
        typeFood,
        typeGun,
        typeTransport,
        animals: merged,
        animalsFilter: merged,
        typeAccommodationFilter: typeAccommodation,
        typeFoodFilter: typeFood
      };
    default:
      return state;
  }
};

export default reducer;
