import React, { Component } from 'react'
import {connect} from 'react-redux'
import {stepLoaded} from '../../../actions'
import './offer.scss'
class  Offer extends Component {
  nextStep = () =>{
    const {stepLoaded} = this.props
    if(localStorage.getItem('token')){
      stepLoaded(2)
    } else {
      stepLoaded(1)
    }
  }
  render () {
    return (
      <div className="offer">
        <h1 className="offer-title">. . .</h1>
        <div className="offer__item">
          <img src={require('../../../assets/img/offer1.png')}/>
          <div className="offer__item__content">
            <p className="offer__item__content-title"> 
              </p>
             <p className="offer__item__content-title offer__item__content-title--margin">.</p> 
            <p className=" offer__item__content-title offer__item__content-title--red">    . </p> 
            <p className="offer__item__content-text">      .         .</p>
            <button className="offer__item__content-button" onClick={this.nextStep}><span>+</span> </button>
          </div>
        </div>
        <div className="offer__item offer__item__reverse">
          <img src={require('../../../assets/img/offer2.png')}/>
          <div className="offer__item__content offer__item__content__reverse">
            <p className="offer__item__content-title offer__item__content-title-reverse">    <span>.</span></p>
            <p className="offer__item__content-text offer__item__content-text-reverse">      .         .</p>
            <button className="offer__item__content-button" onClick={this.nextStep}><span>+</span> </button>
          </div>
        </div>
        <div className="offer__item">
          <img src={require('../../../assets/img/offer3.png')}/>
          <div className="offer__item__content">
              <p className="offer__item__content-title">    <span>100% .</span></p>
              <p className="offer__item__content-text">      .    .</p>
              <button className="offer__item__content-button" onClick={this.nextStep}><span>+</span> </button>
          </div>
        </div>
      </div>
    )
  }
  
}
export default connect(null, {stepLoaded})(Offer);