import React from 'react'
import './steps-progress.scss'

const StepsProgress = ({step}) => {
  let array = [1,2,3,4,5,6,7]
  let progresClass = 'progress-item'
  let progressArray = array.map((progress) => {
    if (progress === step) {
      progresClass += ' progress-item--active'
    } else if (progress < step) {
      progresClass += ' progress-item--past'
    } else {
      progresClass = 'progress-item'
    }
    return <div className={progresClass}></div>
  })
  if(step !== 0 && step < 8) {
    return (
      <div className="progress-wrapper">
        <div className='progress'>
          {progressArray}
        </div>
        <div className='progress-step'>
           {step}  7
        </div>
      </div>
    )
  } 
  return <div></div>

}

export default StepsProgress