import React from "react";
import './textarea.scss'

const TextArea = ({title, name, rows, cols, value, handleChange, placeholder,error,max}) => {
  let errorText;
  if (error) {
    errorText = <span className="field-error">{error}</span>
  }
  return (
    <div className="form-group">
    <div className="form-label">{title}</div>
    <textarea
      className="form-control"
      name={name}
      rows={rows}
      cols={cols}
      value={value}
      maxLength={max}
      onChange={handleChange}
      placeholder={placeholder}
    />
    {errorText}
  </div>
  )
}


export default TextArea;
