import React from 'react'
import MaskedInput from 'react-text-mask'

import './input.scss'

const Input = ({title, name, value, placeholder, handleChange, inputType, mask, count, max, error,handleKeyUp,handleClick}) => {
  let description;
  let inputCustom;
  let countWord;
  let errorText;
  if (title) description = <div className="field-header__title">{title}</div>

  if (count) countWord = <div className="field-header__count">{value.length} / 70<span className="field-header__warning">!</span></div>
  if (mask !== null) {
    inputCustom = <MaskedInput
              type={inputType} name={name} placeholder={placeholder}
              mask={mask}
              guide={false}
              className="field-input"
              value={value}
              maxLength={max}
              onChange={handleChange}
              onClick={handleClick}
            />
  } else {
    inputCustom =  <input
              type={inputType} name={name} placeholder={placeholder}
              className="field-input"
              value={value}
              maxLength={max}
              onChange={handleChange}
              onKeyPress={handleKeyUp}
            />
  }
  if (error) {
    errorText = <span className="field-error">{error}</span>
  }
  return (
    <div className="field">
      <div className="field-header">
        {description} 
        {countWord}
      </div>
      <label htmlFor={name} className="field-label"></label>
      {inputCustom}
      {errorText}
    </div>
  )
}

export default Input