import React, { Component } from "react";
import {connect} from 'react-redux'
import {
  PersonalInfo,
  HuntingDetails,
  HowToGet,
  Food,
  Description,
  Animals,
  Accommodation,
  Success,
  Offer
} from "../pages";
import Banner from "../layout/banner/banner";
import Footer from "../layout/footer/footer";
import StepsProgress from '../form/steps-progress/steps-progress'
import {fetchGetDict,stepLoaded} from '../../actions'

import "./app.scss";

class App extends Component {
  state = {
    newUser: {
      name: ""
    }
  };
  handleInput = e => {
    let value = e.target.value;
    this.setState(
      prevState => ({
        newUser: {
          ...prevState.newUser,
          name: value
        }
      })
      // () => console.log(this.state.newUser.name)
    );
  };
  componentDidMount () {
    const {fetchGetDict,stepLoaded} = this.props
    fetchGetDict()
    if(localStorage.getItem('token')){
      stepLoaded(0)
    }
    
  }
  render() {
    let currentForm;
    const {step} = this.props
    if(step === 0) {
      currentForm = <Offer/>
    } else if(step === 1) {
      currentForm = <PersonalInfo/>
    } else if (step === 2) {
      currentForm = <HuntingDetails/>
    } else if (step === 3) {
      currentForm = <HowToGet/>
    } else if (step === 7) {
      currentForm = <Food/>
    } else if (step === 5) {
      currentForm = <Description/>
    } else if (step === 4) {
      currentForm = <Animals/>
    } else if (step === 6) {
      currentForm = <Accommodation/>
    } else {
      currentForm = <Success/>
    }
    return (
      <div className="app">
        {step === 0 ?<Banner />: ''}
        <main className="main-wrapper">
          <StepsProgress step={step}/> 
          {currentForm}
          {/* <Router>
            <Switch>
              <Route path="/" component={PersonalInfo} exact />
              <Route path="/details" component={HuntingDetails} exact />
              <Route path="/how-to-get" component={HowToGet} exact />
              <Route path="/Food" component={Food} exact />
              <Route path="/Description" component={Description} exact />
              <Route path="/Animals" component={Animals} exact />
              <Route path="/Accommodation" component={Accommodation} exact />
              <Route path="/Success" component={Success} exact />
              <Route render={() => <h2>Page Not Found</h2>} />
            </Switch>
          </Router>  */}
        </main>
        <Footer />
      </div>
    );
  }
}

const mapStateToProps = ({step}) => {
  return {
    step
  }
}

export default connect(mapStateToProps, {stepLoaded,fetchGetDict})(App);
