import React from 'react'
import './switch.scss'

const Switch = ({name,option,handleChange,selectedOption}) => {
  console.log(selectedOption)
  return (
    <label className="switch">
      <input type="checkbox"                 
                id={name}
                name={name}
                onChange={handleChange}
                value={selectedOption}
                checked={selectedOption}
                type="checkbox"/>
      <span className="slider round">{selectedOption}</span>
    </label>
  )
}

export default Switch