import React, { Component } from "react";
import { connect } from "react-redux";
import SimpleReactValidator from 'simple-react-validator';
import Dropdown from "../../form/dropdown";
import Count from "../../form/count";
import Input from "../../form/input";
import Button from "../../form/button";
import CheckboxBlack from "../../form/checkbox-black";

import "react-datepicker/dist/react-datepicker.css";
import "./animals.scss";
import DatePicker from "react-datepicker";
import ru from "date-fns/locale/ru";

import {updateFilterAnimals,stepLoaded, filterDropdownList,animalsStep, methodsHunt} from '../../../actions'

class Animals extends Component {
  state = {

    
    norms: 1,
    startDate: new Date(),
    endDate: new Date(),
    arrayAnimals: [
      {
        id: 1,
        skills: [],
        guns: [],
        type_hunt: [],
        animalId: '',
        license_price: '',
        cnt_animals: 1,
        startDate: new Date(),
        endDate: new Date(),
        hunt_price: '',
        cnt_days: '',
        firstTitle: ' '
      }
    ],
  };

  componentWillMount() {
    this.validator = new SimpleReactValidator({  messages: {
      required: '    ',
      min: '    '
    }})
  }

  componentDidMount () {
    const {animals_step} = this.props
    if(animals_step.length > 0 ){
      this.setState({
        arrayAnimals: animals_step
      })
    }

  }
  removeNorms = (i) => {
    let stateCopy = Object.assign({}, this.state);
    stateCopy.arrayAnimals = stateCopy.arrayAnimals.slice();
    stateCopy.arrayAnimals[i]= Object.assign({}, stateCopy.arrayAnimals[i]);
    let count = stateCopy.arrayAnimals[i].cnt_animals - 1;
    stateCopy.arrayAnimals[i].cnt_animals = count;
    this.setState(stateCopy)
  };
  addNorms = (i) => {
    
    let stateCopy = Object.assign({}, this.state);
    stateCopy.arrayAnimals = stateCopy.arrayAnimals.slice();
    stateCopy.arrayAnimals[i]= Object.assign({}, stateCopy.arrayAnimals[i]);
    let count = stateCopy.arrayAnimals[i].cnt_animals + 1;
    stateCopy.arrayAnimals[i].cnt_animals = count;
    this.setState(stateCopy)
  };
  nextStep = () => {
    const {animalsStep,stepLoaded} = this.props
    const {arrayAnimals}= this.state
    if (this.validator.allValid()) {
      // stepLoaded(8)
      
      animalsStep(arrayAnimals)
      stepLoaded(5);
    } else {
      console.log('qwe')
      this.validator.showMessages();
      this.forceUpdate();
    }

  };
  prevStep = () => {
    const {animalsStep,stepLoaded} = this.props
    const {arrayAnimals}= this.state
    animalsStep(arrayAnimals)
    stepLoaded(3)
  };
  handleChangeStart = (date,i) => {
    let stateCopy = Object.assign({}, this.state);
    stateCopy.arrayAnimals = stateCopy.arrayAnimals.slice();
    stateCopy.arrayAnimals[i]= Object.assign({}, stateCopy.arrayAnimals[i]);
    stateCopy.arrayAnimals[i].startDate = date;
    this.setState(stateCopy)
  };
  handleChangeEnd = (date,i) => {
    let stateCopy = Object.assign({}, this.state);
    stateCopy.arrayAnimals = stateCopy.arrayAnimals.slice();
    stateCopy.arrayAnimals[i]= Object.assign({}, stateCopy.arrayAnimals[i]);
    stateCopy.arrayAnimals[i].endDate = date;
    this.setState(stateCopy)
  };
  deleteAnimal = (i) => {
    if (this.state.arrayAnimals.length > 1) {
      this.setState((state) => {
        const list = state.arrayAnimals.filter((item, j) => i !== j);
        return {
          arrayAnimals:list
        }
      })
    }
  }
  addAnimal = () => {
    console.log('qwe')
    this.setState(state => {
      let animal = {
        id: state.arrayAnimals.length + 1,
        skills: [],
        guns: [],
        type_hunt: [],
        animalId: '',
        license_price: '',
        cnt_animals: 1,
        startDate: new Date(),
        endDate: new Date(),
        hunt_price: '',
        cnt_days: '',
        firstTitle: ' '
      }
      const list = state.arrayAnimals.concat(animal);

      return {
        arrayAnimals:list
      };
    });
  };
  search = (e)=>{
    const {filterDropdownList} = this.props
    const value = e.target.value
    filterDropdownList(value)
  }
  updateFilter = () => {
    const {updateFilterAnimals} =this.props
    updateFilterAnimals()
  }
  handleChangeGuns = (e,i)=> {
    console.log(e.target.value)
    console.log(i)
    let value = e.target.value
    let stateCopy = Object.assign({}, this.state);
    stateCopy.arrayAnimals = stateCopy.arrayAnimals.slice();
    stateCopy.arrayAnimals[i]= Object.assign({}, stateCopy.arrayAnimals[i]);
    stateCopy.arrayAnimals[i].guns = value;
    this.setState(stateCopy)
    // this.setState(prevState => ({
    //   newUser: { ...prevState.newUser, guns:  value }
    // }));
  }
  handleChangeTypeHunt = (e,i)=> {
    console.log(e.target.value)
    let value = e.target.value
    let stateCopy = Object.assign({}, this.state);
    stateCopy.arrayAnimals = stateCopy.arrayAnimals.slice();
    stateCopy.arrayAnimals[i]= Object.assign({}, stateCopy.arrayAnimals[i]);
    stateCopy.arrayAnimals[i].type_hunt = value;
    this.setState(stateCopy)
  }
  handleChangeHuntList = (e,i, name) => {
    const newSelection = e.target.value
    console.log(typeof newSelection)
    let newSelectionArray;
    console.log(this.state.arrayAnimals[i][name])
    if (this.state.arrayAnimals[i][name].indexOf(newSelection) > -1) {
      newSelectionArray = this.state.arrayAnimals[i][name].filter(
        
        s => s !== newSelection 
        
      );
    } else {
      console.log('qwe')
      newSelectionArray = [...this.state.arrayAnimals[i][name], newSelection];
    }

    let stateCopy = Object.assign({}, this.state);
    stateCopy.arrayAnimals = stateCopy.arrayAnimals.slice();
    stateCopy.arrayAnimals[i]= Object.assign({}, stateCopy.arrayAnimals[i]);
    stateCopy.arrayAnimals[i][name] = newSelectionArray;
    this.setState(stateCopy)
  }
  chooseName =({id,name,methods},i) => {
    const {methodsHunt} = this.props
    let stateCopy = Object.assign({}, this.state);
    stateCopy.arrayAnimals = stateCopy.arrayAnimals.slice();
    stateCopy.arrayAnimals[i]= Object.assign({}, stateCopy.arrayAnimals[i]);
    stateCopy.arrayAnimals[i].animalId = id;
    stateCopy.arrayAnimals[i].firstTitle = name;
    this.setState(stateCopy)
    methodsHunt(methods)
  }
  handleInput= (e, name,i) => {
    let value = e.target.value;
    let stateCopy = Object.assign({}, this.state);
    stateCopy.arrayAnimals = stateCopy.arrayAnimals.slice();
    stateCopy.arrayAnimals[i]= Object.assign({}, stateCopy.arrayAnimals[i]);
    stateCopy.arrayAnimals[i][name] = value;
    this.setState(stateCopy);
  };
  render() {
    const { norms, arrayAnimals, newUser } = this.state;
    const {typeHunt, typeMethodHunt,  typeGun, animals}  = this.props

    const animalsArray = arrayAnimals.map((animal, index,) => {
      return (
        <section className="animals">
          <div className="animals-title"> </div>
          <div className="animals-list">
            <Dropdown firstTitle={animal.firstTitle} list={animals} handleSearch={this.search} updateFilter={this.updateFilter} giveName={(item)=>this.chooseName(item,index)} error={this.validator.message('1', animal.animalId, 'required')}/>
            <div className="animals-list__count">
              <div className="animals-list__count-text">
                  (   )
              </div>
              <Count
                name="search"
                counter={animal.cnt_animals}
                counterMax={999}
                remove={()=>this.removeNorms(index)}

                add={()=>this.addNorms(index)}
              />
            </div>
            <div className="animals-list__count">
              <div className="animals-list__count-text"> </div>
              <Input
                inputType="text"
                name="  "
                mask={[
                  "",
                  " ",
                  /[1-9]/,
                  /\d/,
                  /\d/,
                  /\d/,
                  /\d/,
                  /\d/,
                  /\d/,
                  /\d/
                ]}
                value={animal.license_price}
                placeholder={""}
                handleChange={(e) =>this.handleInput(e, 'license_price',index)}
                error={this.validator.message('2', animal.license_price, 'required|min:3')}
              />
            </div>
            <div className="animals-list__count">
              <div className="animals-list__count-text animals-list__count-text--width">
                  (  1  )
              </div>
              <Input
                inputType="text"
                name="  "
                mask={[
                  "",
                  " ",
                  /[1-9]/,
                  /\d/,
                  /\d/,
                  /\d/,
                  /\d/,
                  /\d/,
                  /\d/,
                  /\d/
                ]}
                value={animal.hunt_price}
                placeholder={""}
                handleChange={(e) =>this.handleInput(e, 'hunt_price',index)}
                error={this.validator.message('3', animal.hunt_price, 'required|min:3')}
              />
            </div>
          </div>
          <div>
            <div className="animals-title animals-title--margin">
               
            </div>
            <div className="animals-list">
              <div className="animals-list__calendar">
                <div>
                  <div className="animals-list__calendar-title"></div>
                  <DatePicker
                    selected={animal.startDate}
                    selectsStart
                    startDate={animal.startDate}
                    endDate={animal.endDate}
                    onChange={(date)=>this.handleChangeStart(date,index)}
                    dateFormat="d MMM yyyy"
                    dateFormatCalendar={"MMMM yyyy"}
                    locale={ru}
                  />
                </div>
                <div className="animals-list__calendar-line">-</div>
                <div>
                  <div className="animals-list__calendar-title"></div>
                  <DatePicker
                    dateFormat="d MMM yyyy"
                    dateFormatCalendar={"MMMM yyyy"}
                    locale={ru}
                    selected={animal.endDate}
                    selectsEnd
                    startDate={animal.startDate}
                    endDate={animal.endDate}
                    onChange={(date)=>this.handleChangeEnd(date,index)}
                    minDate={animal.startDate}
                    popperPlacement="bottom-end"
                  />
                </div>
              </div>
              <div className="animals-list__count">
                <div className="animals-list__count-text animals-list__count-text--width">
                  
              </div>
                <div className="animals-list__count-wrap">
                  <div className="animals-list__calendar-title animals-list__calendar-title--opacity">notitle</div>

                  <Input
                    inputType="text"
                    name="  "
                    mask={[
                      /[1-9]/,
                      /\d/,
                      /\d/,
                      /\d/,
                      /\d/,
                      /\d/,
                      /\d/,
                      /\d/
                    ]}
                  value={animal.cnt_days}
                  max={3}
                  handleChange={(e) =>this.handleInput(e, 'cnt_days',index)}
                  error={this.validator.message('4', animal.cnt_days, 'required')}
                  />
                </div>
              </div>
            </div>

          </div>
          <div className="animals-list">
            <div>
              <div className="animals-title animals-title--margin">
                 
              </div>
              <div className="checkbox-list--flex">
                {
                  typeGun.map(({ id, name, svg }) => {
                    return <CheckboxBlack count={index} title={name} id={id} svg={svg} type={"checkbox"} handleChange={(e)=>this.handleChangeHuntList(e,index, 'guns')} selectedOptions={animal.guns} />;
                  })
                }
                {this.validator.message('5', animal.guns.join(''), 'required')}
              </div>
              
            </div>
            <div>
              <div className="animals-title animals-title--margin">
                 
              </div>
              <div className="checkbox-list--flex">
                {
                  typeHunt.map(({ id, name, svg }) => {
                    return <CheckboxBlack count={index} title={name} id={id} svg={svg} type={"checkbox"} handleChange={(e)=>this.handleChangeHuntList(e,index, 'type_hunt')} selectedOptions={animal.type_hunt} />;
                  })
                }
                {this.validator.message('6', animal.type_hunt.join(''), 'required')}
              </div>
            </div>

          </div>
          <div>
            <div className="animals-title animals-title--margin">
               
            </div>
            <div className="checkbox-list--flex">
              {
                typeMethodHunt.map(({ id, name, svg }) => {
                  return <CheckboxBlack count={index} title={name} id={id} svg={svg} type={"checkbox"} handleChange={(e)=>this.handleChangeHuntList(e,index, 'skills')} selectedOptions={animal.skills}/>;
                })
              }
              {this.validator.message('7', animal.skills.join(''), 'required')}
            </div>
          </div>
          <div className="animals-delete" onClick={() =>this.deleteAnimal(index)}> </div>
        </section>
      )
    })

    return (
      <div className="animals-wrapper">
        <div className="title"></div>
        {animalsArray}
        <div className="animals-add" onClick={this.addAnimal}>
          <div className="animals-add__icon">
            <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" viewBox="0 0 16 16"><g><g><path fill="#242323" d="M7 0h2v7h7v2H9v7H7V9H0V7h7z"/></g></g></svg>
          </div>
          <div className="animals-add__text" >  </div>
        </div>
        <div className="animals-btn">
          <Button title="" action={this.prevStep} />
          <Button title="" type="primary" action={this.nextStep} />
        </div>
      </div>
    );
  }
}


const mapStateToProps = ({typeHunt, typeMethodHunt, typeGun,animals,animals_step}) => {
  return {
    typeHunt, 
    typeMethodHunt, 
    typeGun,
    animals,
    animals_step
  }
}
export default connect(
  mapStateToProps,
  {updateFilterAnimals,stepLoaded, filterDropdownList,animalsStep,methodsHunt}
)(Animals);
